.container {
    width: 100%;
    max-width: 1120px;
    height: 100vh;
    margin: 0 auto;

    display: flex;
    align-items: center;
    justify-content: center;

    section.form {
        width: 100%;
        max-width: 350px;
        margin-right: 30px;

        img {
            width: 100%;
        }
        form {
            margin-top: 100px;

            h1 {
                text-align: center;
                font-size: 32px;
                margin-bottom: 32px;
            }
        }
    }
}